@import "pure.css";
@import url("https://unpkg.com/purecss@2.0.3/build/grids-responsive-min.css");
@import "odd.css";
@import "prism.css";
@import url("https://cdn.jsdelivr.net/npm/@docsearch/css@3.9.0/dist/style.css");

/*customizing pure*/

html,
button,
input,
select,
textarea,
.pure-g [class*="pure-u"] {
  /* Set your content font stack here: */
  font-family: -apple-system, ".SFNSText-Regular", "San Francisco", "Roboto", "Segoe UI", "Helvetica Neue",
    "Lucida Grande", sans-serif;
  font-size: 18px;
}

.custom-menu {
  font-size: 17px;
}

.pure-menu-heading {
  position: sticky;
  top: 0;
  display: block;
  width: 325px;
  height: 50px;
  z-index: 999;
  box-sizing: border-box;
  background: #1a252f;
}

.floatingText_body {
  background: bisque;
  padding: 1em 2em;
  border-radius: 0.4em;
}

.floatingText_body h1 {
  font-size: 18px;
}

*|*:link {
  /* color: #1f8dd6;*/
  color: #1f8dd6;
}

span.attribute,
span.att {
  color: #f5844c;
}

.tei_toc h2 {
  display: none;
}

.tei_toc {
  height: calc(100vh - 340px);
  overflow-y: auto;
  overflow-x: hidden;
}

.tei_toc_search {
  padding-top: 20px;
  padding-bottom: 10px;
}

/* Keep search visible just below the heading while the menu scrolls. */
#menu .tei_toc_search {
  position: sticky;
  top: 50px;
  z-index: 998;
  background: #1a252f;
}

#menu a {
  padding-left: 5px;
}

.maintitle,
.footer-odd-subtitle {
  font-family: -apple-system, ".SFNSText-Regular", "San Francisco", Roboto, "Segoe UI", "Helvetica Neue",
    "Lucida Grande", sans-serif;
}

.custom-menu {
  margin-left: -325px !important;
  width: 325px !important;
  background: #1a252f !important;
  overflow: visible !important;
}

/* Ensure long TOC sections scroll within the menu. */
#menu.custom-menu {
  overflow: visible !important;
}

/* Scroll the TOC itself so the menu can stay overflow-visible for dropdowns. */
#menu .toc {
  /* Limit TOC height to the viewport minus the sticky header/search so it can scroll. */
  max-height: calc(100vh - 120px);
  overflow-y: auto;
  overflow-x: hidden;
}

@media (min-width: 48em) {
  .custom-menu {
    left: 325px !important;
  }
}

@media (min-width: 48em) {
  .custom-layout {
    padding-left: 325px !important;
  }
}

.custom-menu.active,
.custom-layout.active,
.custom-layout.active .menu-link {
  left: 325px !important;
}

.pure-menu {
  padding-bottom: 0;
}

.pure-menu-item {
  display: block;
  border: none;
  padding: 0.6em 0 0.6em 0.6em;
}

.pure-menu-item a,
.pure-menu-item span,
a.toc {
  color: #999;
  text-decoration: none;
  white-space: wrap;
  display: inline;
}

.pure-g > div {
  box-sizing: border-box;
}

div.tocTree,
div.toc_body div.toc {
  margin-top: 10px;
  display: flex;
  align-items: flex-start;
  width: 100%;
  flex-wrap: wrap;
}

.toc-heading a.toc {
  /*resets the menu item padding*/
  padding: 0 !important;
  padding-right: 12px !important;
}

.toc-heading:hover a.toc {
  color: #1f8dd6 !important;
}

.tocTree > div:first-child,
div.toc_body div.toc > div:first-child {
  order: 0;
  flex: 0 0 1.5em;
}

/* Controls for first-level TOC entries */

#menu .toc_body > .tocTree > .toc-heading-number,
#menu .toc_body > .toc > .toc-heading-number {
  flex-basis: 3em;
}

#menu .toc_body > .tocTree > .toc-heading,
#menu .toc_body > .toc > .toc-heading {
  max-width: calc(100% - 3.5em);
}

/* Controls for second-level TOC entries */

#menu .toc_body > .tocTree > .continuedtoc > .tocTree > .toc-heading-number,
#menu .toc_body > .tocTree > .continuedtoc > .toc > .toc-heading-number {
  flex-basis: 5.25em;
}

#menu .toc_body > .tocTree > .continuedtoc > .tocTree > .toc-heading,
#menu .toc_body > .tocTree > .continuedtoc > .toc > .toc-heading {
  flex-basis: 11em;
}

#menu .toc_body > .tocTree > .continuedtoc > .toc > .toc-showhide {
  flex-basis: 1.5em;
}

/* Controls for third-level TOC entries */

#menu .toc_body > .tocTree > .continuedtoc > .tocTree > .continuedtoc > .toc > .toc-heading-number {
  flex-basis: 8.4em;
}

#menu .toc_body > .tocTree > .continuedtoc > .tocTree > .continuedtoc > .toc > .toc-heading {
  flex-basis: 9.1em;
}

#menu .toc_body > .tocTree > .continuedtoc > .tocTree > .continuedtoc > .toc > .toc-showhide {
  flex-basis: 0;
  padding-right: 0;
}

/* special provisions for TOC entries starting with the 10th  */

/* second level */

#menu .toc_body > div:nth-child(n + 10) > .continuedtoc > .toc > .toc-heading-number,
#menu .toc_body > div:nth-child(n + 10) > .continuedtoc > .tocTree > .toc-heading-number {
  flex-basis: 5.8em;
}

#menu .toc_body > div:nth-child(n + 10) > .continuedtoc > .toc > .toc-heading,
#menu .toc_body > div:nth-child(n + 10) > .continuedtoc > .tocTree > .toc-heading {
  flex-basis: 10.75em;
}

#menu .toc_body > div:nth-child(n + 10) > .continuedtoc > .toc > .toc-showhide,
#menu .toc_body > div:nth-child(n + 10) > .continuedtoc > .tocTree > .toc-showhide {
  flex-basis: 1.5em;
}

/* third level */
#menu .toc_body > div:nth-child(n + 10) > .continuedtoc > .tocTree > .continuedtoc > .toc > .toc-heading-number {
  flex-basis: 9.4em;
}

.toc-heading-number {
  order: 1;
  text-align: right;
  color: #1f8dd6;
  line-height: 1.4;
}

.toc-heading {
  order: 2;
  flex: 1 1 auto;
  text-align: left;
  min-width: 0;
  line-height: 1.4;
}

.toc-showhide {
  order: 3;
  flex: 0 0 1.5em;
  flex-shrink: 0;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  align-self: flex-start;
  height: 1.4em;
  padding-right: 15px;
}

.tocTree > .continuedtoc,
div.toc_body div.toc > .continuedtoc {
  order: 4;
  flex: 0 0 100%;
}

/* keep span spacing between number and heading */
.toc-heading-number span {
  padding-right: 10px;
}

/*other stuff*/

.tocHide,
.continuedtoc {
  display: none;
}

.tocTree.active > .continuedtoc {
  display: block;
}

.plusminus {
  position: relative;
  width: 14px;
  height: 14px;
  /* left: 20px;*/
  background: #1f8dd6;
  transform: rotate(0deg);
  border-radius: 50%;
  cursor: pointer;
  transition: 0.4s cubic-bezier(0.2, 0.6, 0.3, 1.1);
}

.plusminus:after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 2px;
  width: 50%;
  background: white;
}

.plusminus:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  height: 50%;
  width: 2px;
  background: white;
}

.plusminus.clicked {
  transform: rotate(360deg);
  background: #ed6f59;
}

.plusminus.clicked:before {
  width: 0;
}

/* Handling of example XML code embedded in pages. */
pre.teiCode,
code[class*="language-"],
pre[class*="language-"] {
  white-space: pre-wrap;
}

/* We want our XML code to look like code. */
.xmlTag,
.xmlAttName,
.xmlAttVal,
.teiCode,
span.val {
  font-family: "SFMono-Regular", Consolas, "Liberation Mono", Menlo, Courier, monospace;
}

/* We want our XML code text to be bold. */
.xmlTag,
.xmlAttName,
.xmlAttVal {
  font-weight: bold;
}

/* We want syntax highlighting. */
/* I think I stole these colour values from oXygen. Sorry George! */
.xmlTag {
  color: #000099;
}

.xmlAttName {
  color: #f5844c;
}

.xmlAttVal,
span.val,
.valList .odd_label {
  color: #993300;
}

/*images*/

img {
  display: block;
}

img.padded {
  padding: 1em;
}

div.figure {
  position: relative;
  margin-top: 1em;
  margin-bottom: 2em;
  text-align: center;
}

div.tab div.figure {
  margin-bottom: 1em;
}

div.figure img,
div.thumbnail img {
  border-radius: 0.4em;
  border: 1px solid gainsboro;
  box-shadow: 0 4px 4px -2px rgba(0, 0, 0, 0.5);
  max-width: 100%;
  height: auto;
  box-sizing: border-box;
}

div.caption {
  position: absolute;
  color: slategray;
  font-size: 0.8em !important;
  left: 50%;
  transform: translateX(-50%);
  bottom: 0;
}

div.figure div.caption {
  bottom: -2em;
}

div.gallery div.caption {
  bottom: -1em;
}

div.gallery {
  position: relative;
  margin-bottom: 2em;
  /* Prevent vertical gaps */
  line-height: 0;
  border-radius: 0.4em;
  text-align: center;
  box-shadow: 0 4px 4px -2px rgba(0, 0, 0, 0.5);
}

div.thumbnail {
  float: left;
  width: 100px;
  padding-right: 10px;
}

.gallery img {
  width: 100% !important;
  /*default height; can be overriden in TEI with height attr.*/
  height: 300px !important;
  object-fit: cover;
}

.imgwrapper.top-left img {
  border-top-left-radius: 0.4em;
}

.imgwrapper.top-right img {
  border-top-right-radius: 0.4em;
}

.imgwrapper.bottom-right img {
  border-bottom-right-radius: 0.4em;
}

.imgwrapper.bottom-left img {
  border-bottom-left-radius: 0.4em;
}

/*specification overrides
*/

td.odd_label {
  width: 123px;
}

/*logos*/

.logos {
  padding: 0 !important;
}

.logos .img,
.logos .img-hover {
  width: 160px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

/* .logos .img-hover,
.logos:hover .img {
  display: none;
} */

.logos:hover .img-hover {
  display: block;
}

.dlogo .logos img {
  margin-bottom: 1em;
}

/*terms in lists*/

span.term {
  font-weight: bold;
}

/*table headings*/
tr.label {
  color: white;
  background: #1f8dd6;
}

.pure-table {
  width: 100%;
}

.contributors {
  column-count: 2;
}

.h2020 {
  color: #999;
  font-size: 0.7em;
  padding: 0.5em 2em 0.5em 2em;
}

.h2020 p {
  border-top: 1px solid;
  border-bottom: 1px solid;
  padding: 1em 0;
  text-align: center;
}

.pure-menu-list {
  padding-top: 12px;
  background-image: url("../images/dkbl.png");
}

/* Accordion styles for examples */

.tabs {
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 4px -2px rgba(0, 0, 0, 0.5);
  margin: 1em 0 1em 0;
  padding: 0;
}

.tabs pre[class*="language-"] {
  margin: 0;
}

.tabs input {
  position: absolute;
  opacity: 0;
  z-index: -1;
}

.tab {
  width: 100%;
  color: #fff;
  overflow: hidden;
}

.tab-label {
  display: -webkit-box;
  display: flex;
  -webkit-box-pack: justify;
  justify-content: space-between;
  padding: 0.25em 1em;
  background: #2c3e50;
  font-weight: bold;
  cursor: pointer;
  align-items: center;
  font-size: 0.9em;
  /* Icon */
}

.tab-label:hover {
  background: #1a252f;
}

.tab-label::after {
  content: "\276F";
  width: 1em;

  text-align: center;
  -webkit-transition: all 0.35s;
  transition: all 0.35s;
}

.tab-label .pure-img {
  height: 18px;
}

span.label img {
  display: inline-block;
  margin-right: 1em;
}

.tab-content {
  max-height: 0;
  /*padding: 0 1em;*/
  color: #2c3e50;
  -webkit-transition: all 0.35s;
  transition: all 0.35s;
  background: #f2faff;
  overflow: hidden;
}

.infoPanels .tab-content {
  background: bisque;
}

.infoPanels .tab-content div.panelContent {
  padding: 0 1em;
}

.tab-content .figure {
  margin-left: 0.9em;
  margin-right: 0.9em;
}

.tab-close {
  display: -webkit-box;
  display: flex;
  -webkit-box-pack: end;
  justify-content: flex-end;
  padding: 1em;
  font-size: 0.75em;
  background: #2c3e50;
  cursor: pointer;
}

.tab-close:hover {
  background: #1a252f;
}

.tab.drop {
  border-bottom: 1px solid white;
}

.tabs input:checked + .tab-label {
  background: #1f8dd6;
}

.tabs input:checked + .tab-label::after {
  transform: rotate(90deg);
}

.tabs input:checked ~ .tab-content {
  max-height: 1000vh;
  /*padding: 0 0.5em 0 0;*/
}

.panelContent .code-toolbar {
  margin-bottom: 1em;
}

div.code-toolbar > .toolbar {
  right: 0.6em;
}

div.cit {
  padding-left: 2em;
}

span.italic {
  font-style: italic;
}

span.label {
  font-weight: bold;
}

/* search input width here */
:root {
  --docsearch-primary-color: #1f8dd6;
  --docsearch-container-background: rgba(26, 37, 47, 0.85);
}

/* Keep the DocSearch modal above sticky headers/banners. */
.DocSearch-Container {
  z-index: 10000;
}

/* Sidebar button styling (replaces the old inline input). */
#docsearch .DocSearch-Button {
  width: 16em;
  margin: 0 0 0 1em;
  border-radius: 14px;
  height: 34px;
  background: #fff;
  color: #1c1e21;
  font-weight: 400;
}

#docsearch .DocSearch-Button:hover,
#docsearch .DocSearch-Button:focus,
#docsearch .DocSearch-Button:active {
  background: #fff;
  box-shadow: none;
  color: #1c1e21;
}

/* Tweak keycap alignment inside the sidebar button. */
#docsearch .DocSearch-Button-Keys {
  align-items: center;
}

#docsearch .DocSearch-Button-Key {
  top: 0;
  padding-bottom: 0;
  line-height: 18px;
  color: #9ca3af;
}

#docsearch .DocSearch-Button-Key:last-child {
  font-size: 0.75em;
}

#docsearch .DocSearch-Button .DocSearch-Search-Icon {
  color: #9ca3af;
  width: 16px;
  height: 16px;
}

#docsearch .DocSearch-Button-Placeholder {
  color: #9ca3af;
  font-size: 17px;
}

/*labels in revision history*/

.button-xsmall {
  padding: 0.1em 0.4em;
  background-color: #1a252f;
  color: white;
  border-radius: 0.3em;
  margin-right: 0.3em;
  font-size: medium;
  vertical-align: text-top;
}

blockquote.quote {
  border-left-color: #1f8dd6;
  border-left-width: 4px;
  border-left-style: solid;
  padding-left: 1em;
  margin-left: 20px;
}

.classSpecAttDefs {
  display: inline;
  padding-inline-start: 0px;
}

.classSpecAttDefs li.item {
  display: inline;
  padding-inline-start: 0px;
  color: #f5844c;
}

.classSpecAttDefs li.item:first-child {
  padding-inline-start: 5px;
}

.classSpecAttDefs li.item + li.item::before {
  content: ", ";
  padding-right: 0px;
}

.classSpecItem a.link_ref::after {
  content: ": ";
  text-decoration: none;
  display: inline-block;
}

/* in data types */
div.parent a.link_odd_dataSpec {
  display: block;
}

div.parent ul {
  margin-block-start: 0em;
}

/* Navigation layout in grid-nav: Previous (left), Up (center), Next (right) */
.content {
  display: flex;
  flex-direction: column;
  padding: 2em 1.5em;
  width: 100%;
  max-width: 800px;
  margin: 0 auto;
  box-sizing: border-box;
  overflow-wrap: anywhere;
}

.content > * {
  order: 3;
}

/* Keep footer at or below viewport bottom on short pages. */
#main {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

#main .header {
  border-bottom: 0;
  padding: 0;
}

#main .header-inner {
  max-width: 800px;
  margin: 0 auto;
  padding: 2.5em 2em 0;
  box-sizing: border-box;
  border-bottom: 1px solid #e6eaee;
}

#main .content {
  flex: 1 0 auto;
}

/* Footer: logos left/right, license centered. */
#main .footer {
  margin-top: auto;
  padding: 0;
}

#main .footer-inner {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #334155;
  font-weight: 100;
  max-width: 800px;
  margin: 0 auto;
  padding: 1.25em 1.25em 1.25em;
  box-sizing: border-box;
  border-top: 1px solid #e6eaee;
}

#main .footer-text-row {
  justify-self: center;
  text-align: center;
  font-size: 0.85em;
  margin-bottom: 1em;
  letter-spacing: 0.5px;
}

#main .xs-mobile {
  display: none;
  width: 100%;
}

#main .xs-mobile-row {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1.25em;
}

#main .xs-mobile-row-top {
  margin-bottom: 1em;
}

#main .xs-mobile-row-top {
  display: grid;
  grid-template-columns: 1fr max-content 1fr;
  align-items: center;
  column-gap: 0.85em;
}

#main .xs-mobile-row-top > :nth-child(1) {
  justify-self: end;
}

#main .xs-mobile-row-top > :nth-child(2) {
  justify-self: center;
}

#main .xs-mobile-row-top > :nth-child(3) {
  justify-self: start;
}

#main .xs-mobile-row-bottom {
  justify-content: space-between;
  max-width: 540px;
  margin: 0 auto;
  /* padding: 0 1.25em; */
}

#main .xs-mobile .footer-logo {
  transform: none;
}

#main .footer-mobile {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 1em;
}

#main .footer-builtwith {
  display: grid;
  grid-template-columns: max-content max-content;
  grid-template-rows: auto auto;
  column-gap: 7em;
  /* row-gap: 0.35em; */
  justify-content: center;
  align-items: center;
}

#main .footer-builtwith-labels,
#main .footer-builtwith-logos {
  display: contents;
}

#main .footer-builtwith-label {
  font-size: 13px;
  letter-spacing: 0.04em;
  text-align: center;
  font-variant: small-caps;
  text-transform: none;
  justify-self: center;
}

#main .footer-builtwith-tei,
#main .footer-builtwith-github {
  justify-self: center;
}

#main .footer-logo-tei img {
  height: 30px;
  width: auto;
  image-rendering: auto;
}

#main .footer-logo-github img {
  height: 20px;
  width: auto;
  image-rendering: auto;
}

#main .footer-logo-github {
  transform: translateY(0px);
}

#main .footer-main {
  display: grid;
  grid-template-columns: 1fr max-content 1fr;
  /* row-gap: 0.35em; */
  column-gap: 2em;
  align-items: center;
}

#main .footer-main-labels,
#main .footer-main-logos {
  display: contents;
}

#main .footer-main-labels .footer-label:nth-child(1) {
  grid-column: 1;
  grid-row: 1;
  justify-self: center;
}

#main .footer-main-labels .footer-label:nth-child(2) {
  grid-column: 2;
  grid-row: 1;
  justify-self: center;
}

#main .footer-main-labels .footer-label:nth-child(3) {
  grid-column: 3;
  grid-row: 1;
  justify-self: center;
}

#main .footer-main-logos .footer-left {
  grid-column: 1;
  grid-row: 2;
  justify-self: center;
}

#main .footer-main-logos .footer-center {
  grid-column: 2;
  grid-row: 2;
  justify-self: center;
}

#main .footer-main-logos .footer-right {
  grid-column: 3;
  grid-row: 2;
  justify-self: center;
}

@media (max-width: 420px) {
  /* Extra-small screens: dedicated footer layout with no labels. */
  #main .footer-mobile,
  #main .footer-desktop {
    display: none;
  }

  #main .xs-mobile {
    display: block;
  }

  #main .xs-mobile-row-top {
    gap: 2em;
  }

  #main .xs-mobile-row-bottom {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1.25em;
    max-width: none;
    margin: 0;
  }

  #main .footer-logo-tei img {
    height: 24px;
  }

  #main .footer-logo-github img {
    height: 24px;
  }
}

#main .footer-desktop {
  display: none;
  width: 100%;
}

#main .footer-text-row h1,
#main .footer-text-row h2,
#main .footer-text-row h3,
#main .footer-text-row h4,
#main .footer-text-row h5,
#main .footer-text-row h6 {
  margin: 0;
  font-size: 13px;
  font-weight: inherit;

  display: block;
}

#main .footer-text-row .footer-odd-subtitle {
  display: block;
  margin-top: 0;
  font-variant: small-caps;
  font-size: 18px;
}

#main .footer-text-row .footer-odd-subtitle::before {
  content: " — ";
}

/* #main .footer-text-row .footer-odd-subtitle::after {
  content: ". ";
} */

#main .footer-text-row .footer-odd-tagline {
  margin-top: 0.15em;
  font-style: italic;
}

#main .footer-label {
  font-size: 13px;
  /* color: #d8dde4; */
  letter-spacing: 0.04em;
  text-align: center;
  flex: 1 1 0;
  font-variant: small-caps;
