.content > .grid-nav:first-of-type {
  order: 1;
}

.content > h2:first-child {
  order: 2;
}

.grid-nav {
  display: grid;
  grid-template-columns: max-content max-content 1fr max-content max-content;
  align-items: center;
  column-gap: 0.35em;
  row-gap: 0;
}

.grid-nav span,
.grid-nav a.navigation {
  grid-row: 1;
}

/* Replace nav labels with arrows while keeping text for screen readers */
.grid-nav .upLink,
.grid-nav .previousLink,
.grid-nav .nextLink {
  position: relative;
  display: inline-block;
  width: 1em;
  height: 1em;
  overflow: hidden;
  color: transparent;
  white-space: nowrap;
}

.grid-nav .upLink,
.grid-nav .upLink + a.navigation {
  display: none;
}

.grid-nav .upLink::before,
.grid-nav .previousLink::before,
.grid-nav .nextLink::before {
  position: absolute;
  left: 0;
  top: 0;
  text-indent: 0;
  color: #1f8dd6;
  font-size: 1rem;
  line-height: 1;
}

.grid-nav .upLink::before {
  content: "↑";
}

.grid-nav .previousLink::before {
  content: "←";
}

.grid-nav .nextLink::before {
  content: "→";
}

.grid-nav .previousLink {
  grid-column: 1;
}

.grid-nav .previousLink + a.navigation {
  grid-column: 2;
}

.grid-nav .upLink {
  grid-column: 4;
}

.grid-nav .upLink + a.navigation {
  grid-column: 5;
}

.grid-nav .nextLink {
  grid-column: 5;
}

.grid-nav .nextLink + a.navigation {
  grid-column: 4;
}

